"use client"

import { useState, useEffect } from "react"
import { usePermission } from "@/contexts/PermissionContext"
import { Permission } from "@/types/permissions"
import { PermissionGuard } from "@/components/auth/PermissionGuard"
import { Button } from "@/components/ui/button"
import { Plus, Edit, Trash, RefreshCw, AlertCircle, ArrowLeft } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"
import apiClient from "@/services/apiClient"
import router from "next/router"

// Interface para o produto
interface Produto {
  id: number
  codigo: string
  nome: string
  quantidade: number
  preco: number
}

export default function InventoryPage() {
  const { hasPermission } = usePermission()
  const [produtos, setProdutos] = useState<Produto[]>([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState<string | null>(null)

  // Função para carregar produtos
  const carregarProdutos = async () => {
    try {
      setLoading(true)
      setError(null)

      // Verificar se há token no localStorage
      const token = localStorage.getItem("token")
      console.log("Token ao carregar produtos:", token ? "Presente" : "Ausente")

      // Fazer requisição para a API - Caminho corrigido (sem /api/ duplicado)
      const response = await apiClient.get("/produtos")
      console.log("Produtos carregados:", response.data)
      setProdutos(response.data)
    } catch (error: any) {
      console.error("Erro ao carregar produtos:", error)

      // Mensagem de erro mais detalhada
      let mensagemErro = "Não foi possível carregar os produtos. "

      if (error.response) {
        if (error.response.status === 404) {
          mensagemErro += "Endpoint não encontrado. Verifique o caminho da API."
        } else if (error.response.status === 403) {
          mensagemErro += "Você não tem permissão para acessar este recurso."
        } else {
          mensagemErro += `Erro ${error.response.status}: ${error.response.data?.message || error.response.statusText}`
        }
      } else if (error.request) {
        mensagemErro += "Servidor não respondeu. Verifique se o servidor está rodando."
      } else {
        mensagemErro += error.message
      }

      setError(mensagemErro)
    } finally {
      setLoading(false)
    }
  }

  // Carregar produtos ao montar o componente
  useEffect(() => {
    carregarProdutos()
  }, [])
  

  return (
    <div className="flex-1 space-y-4 p-4 md:p-8 pt-6">
                    <Button 
                variant="outline" 
                size="sm" 
                onClick={() => router.push("/dashboard")} 
                className="flex items-center gap-1">
                <ArrowLeft className="h-4 w-4" />
                <span>Voltar para Dashboard</span>
              </Button>
      
      <div className="flex items-center justify-between">
        <h2 className="text-3xl font-bold tracking-tight">Estoque</h2>
        <div className="flex gap-2">
          <Button variant="outline" onClick={carregarProdutos} disabled={loading}>
            <RefreshCw className={`mr-2 h-4 w-4 ${loading ? "animate-spin" : ""}`} />
            Atualizar
          </Button>

          <PermissionGuard permission={Permission.ADD_INVENTORY}>
            <Button>
              <Plus className="mr-2 h-4 w-4" />
              Adicionar Item
            </Button>
          </PermissionGuard>
        </div>
      </div>

      {/* Mensagem de erro */}
      {error && (
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
      )}

      {/* Tabela de itens do estoque */}
      <div className="rounded-md border">
        <table className="w-full">
          <thead>
            <tr className="border-b bg-muted/50">
              <th className="p-2 text-left">Código</th>
              <th className="p-2 text-left">Nome</th>
              <th className="p-2 text-left">Quantidade</th>
              <th className="p-2 text-left">Preço</th>
              <th className="p-2 text-right">Ações</th>
            </tr>
          </thead>
          <tbody>
            {loading ? (
              <tr>
                <td colSpan={5} className="p-8 text-center">
                  <RefreshCw className="mx-auto h-6 w-6 animate-spin text-primary" />
                  <p className="mt-2 text-sm text-muted-foreground">Carregando produtos...</p>
                </td>
              </tr>
            ) : produtos.length === 0 ? (
              <tr>
                <td colSpan={5} className="p-8 text-center">
                  <p className="text-sm text-muted-foreground">Nenhum produto encontrado.</p>
                </td>
              </tr>
            ) : (
              produtos.map((produto) => (
                <tr key={produto.id} className="border-b">
                  <td className="p-2">{produto.codigo}</td>
                  <td className="p-2">{produto.nome}</td>
                  <td className="p-2">{produto.quantidade}</td>
                  <td className="p-2">R$ {produto.preco.toFixed(2).replace(".", ",")}</td>
                  <td className="p-2 text-right">
                    <div className="flex justify-end gap-2">
                      <PermissionGuard permission={Permission.EDIT_INVENTORY}>
                        <Button variant="ghost" size="icon">
                          <Edit className="h-4 w-4" />
                          <span className="sr-only">Editar</span>
                        </Button>
                      </PermissionGuard>

                      <PermissionGuard permission={Permission.DELETE_INVENTORY}>
                        <Button variant="ghost" size="icon">
                          <Trash className="h-4 w-4" />
                          <span className="sr-only">Excluir</span>
                        </Button>
                      </PermissionGuard>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/* Mensagem para usuários sem permissão de visualização */}
      {!hasPermission(Permission.VIEW_INVENTORY) && (
        <div className="flex justify-center py-8">
          <p className="text-muted-foreground">Você não tem permissão para visualizar o estoque.</p>
        </div>
      )}
    </div>
  )
}
